/**
 * ÐžÐ±Ñ‰Ð°Ñ Ñ‚Ð¾Ñ‡ÐºÐ° Ð²Ñ…Ð¾Ð´Ð°
 * Ð´Ð»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð¿Ð¾Ð»Ð½Ð¾Ð¹ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐ¸ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
 * (Ð²ÐºÐ»ÑŽÑ‡Ð°Ñ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÑƒ Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ð¹, Ð¼ÐµÑ‚Ñ€Ð¸Ðº Ð¸ Ð¿Ñ€Ð¾Ñ‡ÐµÐ³Ð¾).
 *
 * NOTE: ÐÐµ Ð¿ÑƒÑ‚Ð°Ñ‚ÑŒ Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸ÐµÐ¼ DOMContentLoaded
 * (Ð¾Ð½Ð¾ Ð¶Ðµ - $(document).ready(function() {...}) - Ð² Ñ‚ÐµÑ€Ð¼Ð¸Ð½Ð°Ñ… jQuery).
 */
app.createModule("pageLoad", function() {

    // Ð›Ð¾ÐºÐ°Ð»ÑŒÐ½Ñ‹Ðµ Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ðµ
    var module = this;
    var events = [];
    var singleFunctionFired = false;
    var timeoutInit = 120;
    var timeoutDelta = 80;


    /**
     * Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ, ÐºÐ¾Ñ‚Ð¾Ñ€Ð°Ñ ÑÑ€Ð°Ð±Ð¾Ñ‚Ð°ÐµÑ‚ Ð»Ð¸ÑˆÑŒ Ð¾Ð´Ð¸Ð½ Ñ€Ð°Ð·,
     * Ð¸ Ð±ÑƒÐ´ÐµÑ‚ Ð¾Ð·Ð½Ð°Ñ‡Ð°Ñ‚ÑŒ Ð¿Ð¾Ð»Ð½ÑƒÑŽ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÑƒ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹.
     */
    var singleLoadFunction = function() {
        var readyState = document.readyState;
        var func, timeoutCurrent;
        if ((readyState !== "complete" || singleFunctionFired) || !events.length) {
            return false;
        }

        DEBUG && console.log("%c[module pageLoad] window load event fired, count of functions to init: " + events.length + ", timeout for first func: " + timeoutInit + ", timeout delta for other: " + timeoutDelta, "color: purple");

        while (events.length > 0) {
            // Ð’Ð·ÑÑ‚ÑŒ Ð¿ÐµÑ€Ð²Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¼Ð°ÑÑÐ¸Ð²Ð°,
            // Ð¸ Ð¿ÐµÑ€ÐµÐ´Ð°Ñ‚ÑŒ ÐµÐ³Ð¾ Ð² Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½ÑƒÑŽ.
            func = events.shift();

            // Ð‘ÑƒÐ´ÐµÐ¼ Ð·Ð°Ð¿ÑƒÑÐºÐ°Ñ‚ÑŒ Ð½Ð°ÑˆÐ¸ Ð¾Ñ‚Ð»Ð¾Ð¶ÐµÐ½Ð½Ñ‹Ðµ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸
            // Ñ‡ÐµÑ€ÐµÐ· Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»Ñ‘Ð½Ð½Ñ‹Ðµ Ð¿Ñ€Ð¾Ð¼ÐµÐ¶ÑƒÑ‚ÐºÐ¸ Ð²Ñ€ÐµÐ¼ÐµÐ½Ð¸, Ð´Ñ€ÑƒÐ³ Ð·Ð° Ð´Ñ€ÑƒÐ³Ð¾Ð¼ Ñ Ð½ÐµÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¼ Ð¿Ñ€Ð¾Ð¼ÐµÐ¶ÑƒÑ‚ÐºÐ¾Ð¼.
            // ÐœÑ‹ ÑƒÐ¶Ðµ Ð·Ð½Ð°ÐµÐ¼, Ñ‡Ñ‚Ð¾ ÑÑ‚Ð¾ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸,
            // Ð¿Ð¾ÑÑ‚Ð¾Ð¼Ñƒ Ð´Ð¾Ð¿. Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ° Ð½Ðµ Ñ‚Ñ€ÐµÐ±ÑƒÐµÑ‚ÑÑ.
            timeoutCurrent = timeoutCurrent ? timeoutCurrent + timeoutDelta : timeoutInit;
            setTimeout(func, timeoutCurrent);
        }

        singleFunctionFired = true;
        window.removeEventListener("load", singleLoadFunction);
        window.removeEventListener("ready", singleLoadFunction);
    };


    // ÐŸÑ€Ð¸Ð²ÑÐ·ÐºÐ° ÑÐ¾Ð±Ð¸Ñ‚Ð¸Ð¹ Ð¸ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸-Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ°
    if (document.readyState === "complete") {
        singleLoadFunction();
    } else {
        window.addEventListener("load", singleLoadFunction);
        window.addEventListener("ready", singleLoadFunction);
    }


    /**
     * Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ð¾Ð¹ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ð² Ð¾Ñ‡ÐµÑ€ÐµÐ´ÑŒ Ð½Ð° Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ,
     * Ð»Ð¸Ð±Ð¾ Ð½ÐµÐ¼ÐµÐ´Ð»ÐµÐ½Ð½Ð¾Ðµ Ð¸ÑÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸, ÐµÑÐ»Ð¸ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ð° ÑƒÐ¶Ðµ Ð¿Ð¾Ð»Ð½Ð¾ÑÑ‚ÑŒÑŽ Ð·Ð°Ð³Ñ€ÑƒÐ¶ÐµÐ½Ð°.
     *
     * @param {function} func
     */
    var addEvent = function(func) {
        if (typeof func !== "function") {
            return;
        }

        // Ð•ÑÐ»Ð¸ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ð° ÐµÑ‰Ñ‘ Ð½Ðµ Ð·Ð°Ð³Ñ€ÑƒÐ¶ÐµÐ½Ð° Ð¿Ð¾Ð»Ð½Ð¾ÑÑ‚ÑŒ, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð² Ð¾Ñ‡ÐµÑ€ÐµÐ´ÑŒ
        if (!singleFunctionFired) {
            events.push(func);
            DEBUG && console.log("[module pageLoad] function added, count of functions to init now: " + events.length);

        // Ð•ÑÐ»Ð¸ ÑƒÐ¶Ðµ Ð·Ð°Ð³Ñ€ÑƒÐ¶ÐµÐ½Ð°, Ñ‚Ð¾ Ð¿Ñ€Ð¾ÑÑ‚Ð¾ Ð²Ñ‹Ð·Ñ‹Ð²Ð°ÐµÐ¼ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½ÑƒÑŽ Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑŽ
        } else {
            DEBUG && console.log("[module pageLoad] firing function immediately, because window load event already fired");
            func();
        }
    };


    // Ð”Ð¾ÑÑ‚ÑƒÐ¿ Ðº Ñ„ÑƒÐºÑ†Ð¸ÑÐ¼
    // Ñ‚ÐµÐºÑƒÑ‰ÐµÐ³Ð¾ Ð¼Ð¾Ð´ÑƒÐ»Ñ
    module.addEvent = addEvent;
});