/**
 * ÐžÐ±Ñ‰Ð°Ñ Ñ‚Ð¾Ñ‡ÐºÐ° Ð²Ñ…Ð¾Ð´Ð°
 * Ð´Ð»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ ÑÐºÑ€Ð¾Ð»Ð»Ð° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹.
 *
 * NOTE: ÐšÐ¾Ð½Ñ‚Ñ€Ð¾Ð»Ð»ÐµÑ€ Ð±ÑƒÐ´ÐµÑ‚ Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ñ‚ÐµÐºÑƒÑ‰ÐµÐµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ ÑÐºÑ€Ð¾Ð»Ð»Ð°
 * ÐºÐ°Ð¶Ð´Ð¾Ð¹ Ð¸Ð· Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ñ… Ð² Ð´Ð°Ð½Ð½Ñ‹Ð¹ Ð¼Ð¾Ð´ÑƒÐ»ÑŒ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¹.
 */
app.createModule("windowScroll", function() {

    // Ð›Ð¾ÐºÐ°Ð»ÑŒÐ½Ñ‹Ðµ Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ðµ
    var module = this;
    var events = [];
    var enabled = false;


    /**
     * ÐžÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº,
     * ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð¿Ñ€Ð¾Ñ…Ð¾Ð´Ð¸Ñ‚ Ð¿Ð¾ Ð²ÑÐµÐ¼ Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑÐ¼ Ð¸Ð· Ð¼Ð°ÑÑÐ¸Ð²Ð° events
     * Ð¸ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÑÐµÑ‚ Ð¸Ñ….
     */
    var scrollController = function() {
        if (!enabled) {
            return;
        }

        var y = document.body.scrollTop || document.documentElement.scrollTop;
        var f, i, len;
        for (i = 0, len = events.length; i < len; i++) {
            f = events[i];
            f.call(this, y);
        }
    };


    /**
     * ÐŸÑ€Ð¸Ð²ÑÐ·ÐºÐ° ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð¸ ÐºÐ¾Ð½Ñ‚Ñ€Ð¾Ð»Ð»ÐµÑ€Ð°.
     * Ð’Ñ‹Ð½ÐµÑÐµÐ½Ð¾ Ð² Ð¾Ñ‚Ð´ÐµÐ»ÑŒÐ½ÑƒÑŽ Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑŽ, Ñ‚Ð°Ðº ÐºÐ°Ðº ÐµÑÐ»Ð¸ Ð¼Ð¾Ð´ÑƒÐ»ÑŽ Ð½Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð° Ð½Ð¸ Ð¾Ð´Ð½Ð° Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð½Ð° ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ðµ "scroll"
     * Ñ‚Ð¾ Ð¸ Ð¾Ð±Ñ‰Ð¸Ð¹ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð²ÐµÑˆÐ°Ñ‚ÑŒ Ð½ÐµÑ‚ ÑÐ¼Ñ‹ÑÐ»Ð°.
     * Ð¡Ð´ÐµÐ»Ð°Ð½Ð¾ Ð´Ð»Ñ Ð¾Ð¿Ñ‚Ð¸Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ð¸ Ð¿Ñ€Ð¾Ð¸Ð·Ð²Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ÑÑ‚Ð¸ (Ð½ÐµÐ±Ð¾Ð»ÑŒÑˆÐ°Ñ Ð¿Ñ€Ð¸Ð±Ð°Ð²ÐºÐ°).
     */
    var bind = function() {
        window.addEventListener("scroll", scrollController);
    };


    /**
     * Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ð¾Ð¹ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ð² Ð¼Ð°ÑÑÐ¸Ð² Ð²ÑÐµÑ… Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¹,
     * ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð±ÑƒÐ´ÑƒÑ‚ Ð²Ñ‹Ð·Ð²Ð°Ð½Ñ‹ Ð¿Ñ€Ð¸ ÑÑ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°Ð½Ð¸Ð¸ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ "scroll".
     *
     * @param {function} func
     */
    var addEvent = function(func) {
        if (typeof func !== "function") {
            return;
        }

        if (!enabled) {
            enabled = true;
            bind();
        }

        events.push(func);
        DEBUG && console.log("[module windowScroll] function added, count of functions to init now: " + events.length);
    };


    // Ð”Ð¾ÑÑ‚ÑƒÐ¿ Ðº Ñ„ÑƒÐºÑ†Ð¸ÑÐ¼
    // Ñ‚ÐµÐºÑƒÑ‰ÐµÐ³Ð¾ Ð¼Ð¾Ð´ÑƒÐ»Ñ
    module.addEvent = addEvent;
});