/**
 * ÐžÐ±Ñ‰Ð°Ñ Ñ‚Ð¾Ñ‡ÐºÐ° Ð²Ñ…Ð¾Ð´Ð°
 * Ð´Ð»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ Ñ€Ð°Ð·Ð¼ÐµÑ€Ð¾Ð² ÑÐºÑ€Ð°Ð½Ð°.
 *
 * NOTE: Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð½Ð° Ð¿Ñ€Ð°ÐºÑ‚Ð¸ÐºÐµ Ð¿Ñ€Ð¾Ð¸ÑÑ…Ð¾Ð´Ð¸Ñ‚ Ñ€ÐµÐ´ÐºÐ¾
 * (Ð½Ð°Ð¿Ñ€Ð¸Ð¼ÐµÑ€, Ð¿Ð¾Ð²Ð¾Ñ€Ð¾Ñ‚ Ð¿Ð»Ð°Ð½ÑˆÐµÑ‚Ð° Ð¸Ð»Ð¸ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð°, Ð¸Ð»Ð¸ Ñ€Ð°Ð²Ð·ÐµÑ€Ð½ÑƒÑ‚ÑŒ Ð¾ÐºÐ½Ð¾ Ð½Ð° Ð¿Ð¾Ð»Ð½Ñ‹Ð¹ ÑÐºÑ€Ð°Ð½ Ð½Ð° Ð´ÐµÑÐºÑ‚Ð¾Ð¿Ðµ),
 * Ð¿Ð¾ÑÑ‚Ð¾Ð¼Ñƒ Ð¼Ñ‹ Ð±ÑƒÐ´ÐµÐ¼ "Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÑÑ‚ÑŒ" ÐµÐ³Ð¾ Ñ Ð¿Ð¾Ð¼Ð¾Ñ‰ÑŒÑŽ debouncer
 * (Ð¾Ñ‚Ð»Ð¾Ð¶ÐµÐ½Ð½Ð¾Ðµ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ).
 */
app.createModule("windowResize", function() {

    // Ð›Ð¾ÐºÐ°Ð»ÑŒÐ½Ñ‹Ðµ Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ðµ
    var module = this;
    var events = [];


    /**
     * ÐžÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº,
     * ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð¿Ñ€Ð¾Ñ…Ð¾Ð´Ð¸Ñ‚ Ð¿Ð¾ Ð²ÑÐµÐ¼ Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑÐ¼ Ð¸Ð· Ð¼Ð°ÑÑÐ¸Ð²Ð° events
     * Ð¸ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÑÐµÑ‚ Ð¸Ñ….
     *
     * Ð’Ñ‹Ð·Ñ‹Ð²Ð°ÐµÐ¼ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ñ‡ÐµÑ€ÐµÐ· debouncer (Ð·Ð°Ð´ÐµÑ€Ð¶ÐºÐ° Ð¿Ð¾ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸ÑŽ),
     * Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ð¸Ð· Ð¼Ð°ÑÑÐ¸Ð²Ð° Ð½Ðµ Ð²Ñ‹Ð·Ñ‹Ð²Ð°Ð»Ð¸ÑÑŒ ÑÐ»Ð¸ÑˆÐºÐ¾Ð¼ Ñ‡Ð°ÑÑ‚Ð¾.
     */
    var resizeController = app.utils.debouncer(function() {
        if (!events.length) {
            return;
        }

        var f, i, len;
        for (i = 0, len = events.length; i < len; i++) {
            f = events[i];
            f();
        }
    });


    /**
     * Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ð¾Ð¹ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ð² Ð¼Ð°ÑÑÐ¸Ð² Ð²ÑÐµÑ… Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¹,
     * ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð±ÑƒÐ´ÑƒÑ‚ Ð²Ñ‹Ð·Ð²Ð°Ð½Ñ‹ Ð¿Ñ€Ð¸ ÑÑ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°Ð½Ð¸Ð¸ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ "resize" Ð¾ÐºÐ½Ð° Ð±Ñ€Ð°ÑƒÐ·ÐµÑ€Ð°.
     *
     * @param {function} func
     */
    var addEvent = function(func) {
        if (typeof func !== "function") {
            return;
        }

        events.push(func);
        DEBUG && console.log("[module windowResize] function added, count of functions to init now: " + events.length);
    };


    // ÐŸÑ€Ð¸Ð²ÑÐ·ÐºÐ° ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð¸ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ°
    window.addEventListener("resize", resizeController);


    // Ð”Ð¾ÑÑ‚ÑƒÐ¿ Ðº Ñ„ÑƒÐºÑ†Ð¸ÑÐ¼
    // Ñ‚ÐµÐºÑƒÑ‰ÐµÐ³Ð¾ Ð¼Ð¾Ð´ÑƒÐ»Ñ
    module.addEvent = addEvent;
});